import React, { useContext } from "react";
import Grid from "@material-ui/core/Grid";
import Card from "@material-ui/core/Card";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import DeleteIcon from "@material-ui/icons/Delete";
import { QuiverContext } from "./context.quiver";
import Button from "@material-ui/core/Button";

export default function Quiver() {
  const { quiver, deleteBoard,  openDialog } = useContext(QuiverContext);
  return (
    <div>
      <Grid container spacing={3}>
        {quiver.map(b => (
          <Grid item xs={4} key={b.key}>
            <Card>
              <Typography gutterBottom variant="h6" component="h2">
                {b.model}
              </Typography>
              <Typography
                style={{ display: "inline-block" }}
                variant="body2"
                color="textSecondary"
                component="p"
              >
                {`${b.board.height1}'${b.board.height2}" x ${b.board.width1}" ${b.board.width2}/${b.board.width3} x ${b.board.thickness1}"  ${b.board.thickness2}/${b.board.thickness3} ${b.board.volume}L`}
              </Typography>
              <Button style={{padding:'1px', margin:'10px'}} variant='contained' size='small'
               onClick={() => openDialog(b)}>
                  Custom
                </Button>
              <IconButton
                style={{ display: "inline-block", color: 'grey' }}
                aria-label="delete"
                size="small"
                onClick={() => deleteBoard(b.key)}
              >
                
                <DeleteIcon fontSize="inherit" />
              </IconButton>
              <CardMedia
                component="img"
                alt="surfboardPicture"
                height="300"
                image={b.image}
                title="surfboard"
                style={{
                  objectFit: "contain",
                  justifyContent: "flex-start"
                }}
              />
            </Card>
          </Grid>
        ))}
      </Grid>
    </div>
  );
}
