import { models } from './store';
import React ,{useContext} from 'react';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import uuid from 'uuid';
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import {QuiverContext} from './context.quiver';

export default function BoardList() {
  const { addToQuiver } = useContext(QuiverContext);
  return (
    <div>
      <Grid container spacing={3}>
      {  models.map(model => (
        <Grid item xs={6} key={model.model}>
          <Card>
          <Grid container spacing={0}>
                <Grid item xs={8}>
                  <CardMedia
                    component="img"
                    alt="surfboardPicture"
                    height="300"
                    image={model.image}
                    title="surfboard"
                    style={{
                      objectFit: "contain",
                      justifyContent: "flex-start"
                    }}
                  />
                </Grid>
                <Grid item xs={4} style={{ height: 300, overflow: "auto" }}>
                  <Typography gutterBottom variant="h6" component="h2">
                    {model.model}
                  </Typography>
                  {model.dimensions.map(
                    board => (
                      <div key={uuid()}>
                      <Typography style={{display:'inline-block'}} variant="body2" color="textSecondary" component="p">
        {`${board.height1}'${board.height2}" x ${board.width1}" ${board.width2}/${board.width3} x ${board.thickness1}"  ${board.thickness2}/${board.thickness3} ${board.volume}L`}
        </Typography>
        <IconButton style={{display:'inline-block'}} aria-label="delete" color='secondary' size="small" onClick={()=> addToQuiver(model.model, model.image, board)}>
          <AddIcon fontSize="inherit" />
        </IconButton>
       </div>
                    )
                  )}
                  </Grid>
                  </Grid>
       </Card>
        </Grid>
      ))}
        </Grid> 
    </div>
  )
}
