import React, { useContext, useState } from 'react';
import { QuiverContext } from "./context.quiver";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import useInputState from './hooks/useInputState';
import DialogContentText from "@material-ui/core/DialogContentText";




export default function CustomForm() {
  const { newBoard, initialBoard } = useContext(QuiverContext);
  const [height1, updateHeight1, resetHeight1] = useInputState(newBoard.board.height1);
  const [height2, updateHeight2, resetHeight2] = useInputState(newBoard.board.height2);
  const [width1, updateWidth1, resetWidth1] = useInputState(newBoard.board.width1);
  const [width2, updateWidth2, resetWidth2] = useInputState(newBoard.board.width2);
  const [width3, updateWidth3, resetWidth3] = useInputState(newBoard.board.width3);
  const [thickness1, updateThickness1, resetThickness1] = useInputState(newBoard.board.thickness1);
  const [thickness2, updateThickness2, resetThickness2] = useInputState(newBoard.board.thickness2);
  const [thickness3, updateThickness3, resetThickness3] = useInputState(newBoard.board.thickness3);
  const [volume, setVolume] = useState(newBoard.board.volume);
  
  
  return (
    <div>
      <DialogContent>
      <DialogTitle style={{ display: "inline" }}>
                {initialBoard.model}
              </DialogTitle>
              <DialogContentText style={{ display: "inline" }}>
                {`${initialBoard.board.height1}'${initialBoard.board.height2}" x ${initialBoard.board.width1}"${initialBoard.board.width2}/${initialBoard.board.width3} x ${initialBoard.board.thickness1}"${initialBoard.board.thickness2}/${initialBoard.board.thickness3} 
         ${initialBoard.board.volume}L `}
              </DialogContentText>
       <DialogTitle style={{ display: "inline" }}>
                Enter Custom Dimensions
                <br />
                Height:
              </DialogTitle>
              <TextField
                id="outlined-number"
                label=""
                name="height1"
                index="0"
                type="number"
                margin="normal"
                variant="outlined"
                style={{ width: 60, margin: 0 }}
                value={height1}
                onChange={updateHeight1}
              />
              feet
              <TextField
                id="outlined-number"
                label=""
                name="height2"
                type="number"
                margin="normal"
                variant="outlined"
                style={{ width: 60, margin: 0 }}
                onChange={updateHeight2}
                value={  height2}
              />
              inches
              <DialogTitle style={{ display: "inline" }}>Width:</DialogTitle>
              <TextField
                id="outlined-number"
                label=""
                name="width1"
                type="number"
                margin="normal"
                variant="outlined"
                style={{ width: 60, margin: 0 }}
                onChange={updateWidth1}
                value={  width1}
              />
              and
              <TextField
                id="outlined-number"
                label=""
                name="width2"
                type="number"
                margin="normal"
                variant="outlined"
                style={{ width: 60, margin: 0 }}
                onChange={updateWidth2}
                value={  width2}
              />
              {`/`}
              <TextField
                id="outlined-number"
                label=""
                name="width3"
                type="number"
                margin="normal"
                variant="outlined"
                style={{ width: 60, margin: 0 }}
                onChange={updateWidth3}
                value={  width3}
              />
              inches
              <br />
              <DialogTitle style={{ display: "inline" }}>
                Thickness:
              </DialogTitle>
              <TextField
                id="outlined-number"
                label=""
                name="thickness1"
                type="number"
                margin="normal"
                variant="outlined"
                style={{ width: 60, margin: 0 }}
                onChange={updateThickness1}
                value={  thickness1}
              />
              and
              <TextField
                id="outlined-number"
                label=""
                name="thickness2"
                type="number"
                margin="normal"
                variant="outlined"
                style={{ width: 60, margin: 0 }}
                onChange={updateThickness2}
                value={  thickness2}
              />
              {`/`}
              <TextField
                id="outlined-number"
                label=""
                name="thickness3"
                type="number"
                margin="normal"
                variant="outlined"
                style={{ width: 60, marginTop: 0 }}
                onChange={updateThickness3}
                value={  thickness3}
              />
              inches
              <br />
              <DialogTitle style={{ display: "inline" }}>
                Volume:{  volume}Liters
              </DialogTitle>
            </DialogContent>
      {/* <DialogContent>
              <DialogTitle style={{ display: "inline" }}>
                {newBoard.model}
              </DialogTitle>
              <TextField
                id="outlined-number"
                label=""
                name="height1"
                index="0"
                type="number"
                margin="normal"
                variant="outlined"
                style={{ width: 60, margin: 0 }}
                value={height1}
                onChange={updateHeight1}
              />
            
          </DialogContent> */}
    </div>
  )
}
