import React, { createContext ,useState } from 'react';
import uuid from 'uuid';
import useToggle from './hooks/useToggle';

export const QuiverContext = createContext();

export function QuiverProvider (props) {
const [quiverOn, toggleQuiver] = useToggle(false);
const [dialogOn, toggleDialog] = useToggle(false);
const [quiver, setQuiver] = useState([]);
const [newBoard, setNewBoard] = useState({});
const [initialBoard, setInitialBoard] = useState({});


const openDialog = (customboard) => {
  setNewBoard(customboard);
  setInitialBoard(customboard);
toggleDialog();
};
 
  const deleteBoard = (id) => {
    setQuiver(
   quiver.filter(board => board.key !== id)
  );};

  const addToQuiver = (model, image, board) => {
    setQuiver([...quiver,{ model:model, image:image, board:board, key:uuid() } ]);
    toggleQuiver();};
  
    return (
      <QuiverContext.Provider value={{quiver, quiverOn, newBoard, dialogOn, toggleQuiver, addToQuiver, deleteBoard, toggleDialog, openDialog, initialBoard }}>
        {props.children}
      </QuiverContext.Provider>
    )
  }


